@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
@import url(https://fonts.googleapis.com/css?family=Pacifico);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

canvas {
  display: block;
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0; }

#mypic {
  position: fixed;
  top: 0;
  right: 0;
  width: 37%;
  height: 100%; }

#mylogo {
  width: 15%;
  height: 10%; }

h5 {
  margin-top: 1%;
  font-family: 'Playfair Display';
  font-size: 1.5em; }

#intro {
  width: 60%;
  margin-right: 5%;
  margin-left: 2%; }

#pfont {
  font-family: 'Source Sans Pro';
  font-size: 20px;
  color: #5B94D4;
  margin-top: 3%;
  margin-bottom: 1%; }

#container {
  width: 700px;
  margin: 0; }

#activity {
  width: 300px;
  float: left; }

#time {
  width: 300px;
  margin-left: 150px; }

#item {
  font-family: 'Source Sans Pro';
  font-size: 16px; }

#entry {
  font-family: 'Source Sans Pro';
  font-size: 16px;
  display: inline; }

#cv {
  margin-bottom: 45px; }

#project {
  margin-bottom: 45px; }

#contact {
  display: inline;
  margin-right: 5em; }

a {
  color: black;
  border-bottom: 1px dotted black;
  background-color: transparent;
  text-decoration: none; }
  a:visited {
    color: black;
    border-bottom: 1px dotted black;
    background-color: transparent;
    text-decoration: none; }
  a:hover {
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    border-bottom: 3px solid #5B94D4;
    background-color: transparent;
    text-decoration: none; }

#myIcon {
  margin-left: 0%;
  margin-right: 2%; }

